/**
 * Wordpress Dependencies
 */
import { __ } from "@wordpress/i18n";
export const typeToSubtitle = (type) => {
    switch ((type || "").toLowerCase()) {
        case "select":
            return __("Select text box", "wpcafe");
        case "time":
            return __("Time input", "wpcafe");
        case "date":
            return __("Date input", "wpcafe");
        case "textarea":
            return __("Textarea", "wpcafe");
        case "number":
            return __("Number input", "wpcafe");
        case "location":
            return __("Select Location", "wpcafe");
        case "visual_table":
            return __("Visual table selector", "wpcafe");
        case "food_menu":
            return __("Food menu block", "wpcafe");
        default:
            return __("Text input", "wpcafe");
    }
};
export const fieldTypeOptions = [
    { value: "text", label: __("Input box", "wpcafe") },
    { value: "select", label: __("Dropdown", "wpcafe") },
    { value: "textarea", label: __("Textarea", "wpcafe") },
    { value: "radio", label: __("Radio group", "wpcafe") },
    { value: "checkbox", label: __("Checkbox group", "wpcafe") },
];
// Generate a unique slug-like value from a label
export const generateUniqueOptionValue = (label, takenValues, attemptIndex = 0) => {
    const slugBase = label
        .toLowerCase()
        .replace(/[^a-z0-9]+/g, "-")
        .replace(/^-+|-+$/g, "");
    const candidate = attemptIndex === 0 ? slugBase : `${slugBase}-${attemptIndex}`;
    if (!takenValues.includes(candidate))
        return candidate;
    return generateUniqueOptionValue(label, takenValues, attemptIndex + 1);
};
